.main-menu {
	border-top: 1px solid $gray-2;
	background-color: $primary-color;
	@media (max-width: $bp3) {
		display: none;
	}
}
.index {
	.main-menu {
		background-color: #fff;
	}
}
.main-menu__wrap {
	display: flex;
	align-items: center;
}
.main-menu__item {
	height: 52px;
	flex: 1 1 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;
	text-transform: uppercase;
	font-weight: 700;
	display: flex;
	position: relative;
	&:hover {
		background-color: rgba(0,0,0,.05);
	}
	> a {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #000; // var
	}
}



// popup menu
.popup-catalog-menu {
	position: absolute;
	background-color: #f5f1e5;
	z-index: 2;
	top: 50px;
	left: 0;
	display: flex;
	box-shadow: 0 10px 15px rgba(0,0,0,.2);
	a {
		padding: $gap-sm;
		font-weight: 400;
		text-transform: none;
		&:hover {
			background-color: #fff;
		}
	}
}
.sections {
	width: 200px; 
	display: flex;
	flex-direction: column;
	padding: $gap-sm 0;
}
.subsections {
	display: flex;
	flex-direction: column;
	width: 250px;
	background-color: #fff;
	padding: $gap-sm 0;
}
.popup-text-menu {
	@extend .popup-catalog-menu;
}
// popup menu end