body {
	color: #333;
}
.title_2 {
	font-size: 28.5px;
	margin-bottom: 20px;
}
.sections-block {
	padding-top: 20px;
	padding-bottom: 65px;
}
.banner-sections__title {
	height: 73px;
}
.sections-block_wrap {
	grid-row-gap: 30px;
	grid-column-gap: 26px;
}
.devices-block {
	background-color: #F2F2F2;
}
.content__main {
	.devices-block {
		background-color: unset;
	}
}
.devices-block__column {
	padding: 0 12px;
}
.devices-block__wrap {
	margin: 0 -12px;
}
.device__image {
	margin-bottom: 15px;
}
.device__name {
	line-height: 1.1;
	margin-bottom: 12px;
}
.text_size_sm {
	font-size: 12px;
}
.device {
	.text_size_sm {
		margin-bottom: 12px;
	}
	.device-order {
		margin-bottom: 12px;
	}
}
.device__price {
	margin-bottom: 12px;
	> div {
		margin-right: 19px;
	}
}
input[type=number] {
	font-size: 16px;
	// padding-left: 18px;
}
.order form {
	> * {
		margin-right: 10px;
	}
}
.add {
	> * {
		margin-right: 22px;
	}
}
.device-nav {
	padding-top: 14px;
}
.numbers-block {
	padding: 60px 0;
}
.numbers-block__wrap {
	padding: 0 40px;
}
.banner-numbers__title {
	letter-spacing: 2px;
	font-size: 47px;
}
.banner-numbers__text {
	font-size: 22px;
	font-weight: 400;
}
.blockquote-block {
	padding: 54px 0;
}
.blockquote-block__title {
	font-size: 32px;
	margin-bottom: 10px;
}
.icons-block {
	padding: 57px 0 45px;
	.title {
		font-size: 25.5px;
		margin-bottom: 22px;
	}
}
.icons-block__wrap {
	grid-row-gap: 5px;
}
.banner-icons {
	padding-right: 0;
	padding-left: 0;
}
.banner-icons__image {
	height: 75px;
}
.banner-icons__title {
	font-size: 18px;
	margin-bottom: 10px;
	padding: 0 16px;
}
.banner-icons__text {
	font-size: 15px;
	line-height: 1.3;
	padding: 0 24px;
	color: #828282;
}
.gifts-block {
	padding: 40px 0;
}
.banner-gift {
	padding: 30px 40% 30px 22px;
}
.banner-gift__title {
	font-size: 18px;
	margin-bottom: 17px;
}
.banner-gift__text {
	font-size: 11px;
	line-height: 1.5;
	color: #4D4D4D;
}
.contacts-block {
	padding: 54px 0;
	padding-bottom: 0;
}
.contacts-block_title {
	font-size: 25px;
	margin-bottom: 30px;
}
.contacts-block_subtitle {
	line-height: 1.4;
	font-size: 18.5px;
	max-width: 650px;
	margin-bottom: 21px;
}
.contacts-block_text {
	line-height: 1.45em;
	margin-bottom: 54px;
}
.footer {
	padding-top: 60px;
}
.footer__wrap {
	@media(min-width: $bp1) {
		grid-template-columns: 1.3fr 1.1fr 1fr 1.2fr 0.9fr;
	}
	padding: 34px 0 29px;
}
.footer__column {
	&:first-child {
		.footer__title {
			font-size: 16.5px;
			margin-bottom: 12px;
		}
	}
}
.footer__title {
	margin-bottom: 10px;
}
.footer__text {
	p {
		margin-bottom: 3px;
		line-height: 1.5;
	}
}
.footer__list {
	li {
		margin-bottom: 2px;
	}
}
.forever {
	margin-bottom: 30px;
	img {
		margin-right: 14px;
	}
}
.google-rating__text {
	font-size: 13px;
	line-height: 1.3;
	width: 190px;
	color: #333;
}
.tag {
	font-size: 14px;
	font-weight: 700;
	padding: 5px 14px;
}
.side-menu {
	margin-top: -13px;
	margin-bottom: 36px;
}
.side-menu__item_level_1 {
	font-size: 15px;
	padding: 10.5px;
	padding-left: 16px;
	&:before {
		top: 13px;
		border-width: 7px;
	}
}
.side-menu__item_level_2 {
	padding: 7.4px;
	padding-left: 20px;
	&:before {
		top: 11px;
	}
	&+.side-menu__item_level_1 {
		margin-top: 12px;
	}
}
.path_common {
	margin-bottom: 0;
}
.title_1 {
	font-size: 28px;
	margin-bottom: 34px;
}
.catalog-items__wrap {
	grid-row-gap: 30px;
	grid-column-gap: 30px;
}
.catalog-item__image {
	flex-basis: 129px;
}
.catalog-item__info {
	padding-left: 22px;
	padding-bottom: 16px;
}
.catalog-item__title {
    margin-bottom: 5px;
    line-height: 1.4;
}
.catalog-item__text {
    line-height: 1.3;
	color: #616161;
}
.catalog-item__title:before {
	left: -30px;
    top: 8px;
    width: 20px;
}
.side-banner {
	margin-bottom: 16px;
}
.side-links {
	.title {
		font-size: 22px;
		font-weight: 400;
		margin-bottom: 12px;
	}
}
.side-links__link {
	margin-bottom: 12px;
	line-height: 1.4;
}
.side-info {
	margin-bottom: 28px;
	.title {
		font-size: 20.6px;
		margin-bottom: 12px;
	}
	p {
		font-size: 15px;
		margin-bottom: 16px;
	}
}
.side-button {
	margin-bottom: 27px;
	button {
		font-size: 14.5px;
	}
	&+.side-info {
		a {
			font-size: 14.7px;
		}
	}
}
.title_1 {
	max-width: 770px;
	line-height: 1.1;
}
.section-text {
	margin-top: -10px;
	line-height: 1.45;
	margin-bottom: 10px;
}
.labels__item {
	margin-right: 10px;
	margin-bottom: 10px;
}
.label {
	padding: 7px 18px;
}
.filter_section {
	margin-bottom: 12px;
}
.filter__button {
	font-size: 15px;
	padding: 8px 6px;
	padding-right: 10px;
	.icon {
		margin-right: 0;
	}
}
.filter__actions {
	button {
		background-color: #4DB0C7;
		border-color: #4DB0C7;
		font-size: 15px;
		margin-right: 10px;
	}
}
.filter__wrap {
	grid-gap: 16px 24px;
	margin-bottom: 24px;
}
.filter__values {
	padding: 16px 10px;
}
.filter__value {
	margin-bottom: 15px;
	label {
		font-size: 14px;
		color: black;
	}
	input[type=checkbox] {
		margin-right: 12px;
	}
}
.filter__title {
	padding: 9px 0px;
	padding-left: 10px;
	font-size: 16px;
	color: #000;
}
.view_section {
	padding-bottom: 9px;
	margin-bottom: 20px;
}
.view__column {
	margin-left: -4px;
	.icon-link {
		&:last-child {
			margin-left: 10px;
		}
	}
	&:first-child {
		margin-right: 75px;
	}
	&:nth-child(2) {
		margin-right: 18px;
	}
	&:last-child {
		font-size: 15px;
		select {
			margin-left: 20px;
		}
	}
	select {
		font-size: 15px;
		height: 34px;
		padding: 4px 6px 4px 11px;
		option {
			font-size: 14px;
		}
	}
}
.devices_section {
	margin-bottom: 40px;
}
.devices__wrap {
	grid-gap: 30px 24px;
}
.pagination__label {
	font-size: 15px;
	margin-right: 10px;
}
.pagination__item {
	margin-right: 11px;
	padding: 6px 4px;
}
.pagination__item_active {
	margin-right: 10px;
}

.devices__wrap_rows {
	grid-row-gap: 18px;
}
.device_row {
	border: 1px solid $gray-2;
	&:last-child {
		border: 1px solid $gray-2;
	}
	.add {
		> * {
			margin-right: 0;
			padding-left: 5px;
		}
	}
	.button {
		font-size: 15px;
		padding: 0 10px;
	}
	.device__column {
		padding-top: 12px;
		margin-right: 18px;
		&:first-child {
			padding: 0;
		}
		&:nth-child(3){
			padding-top: 18px;
			margin-right: 14px;
		}
		&:last-child{
			padding-top: 10px;
			margin-right: 20px;
		}
	}
	table.params {
		margin-bottom: 0;
		td, td:first-child {
			padding: 1px;
		}
	}
	.device__image {
		width: 168px;
	}
	.device__name {
		font-size: 15px;
		margin-bottom: 16px;
	}
	.device__price {
		> div {
			font-size: 15px;
		}
		.price__item_old {
			font-size: 14px;
			margin-right: 15px;
		}
	}
}
.path {
	&+.title {
		&+.subtitle {
			margin-top: -25px;
			font-size: 15.5px;
		}
	}
}
.device-basic {
	margin-top: 35px;
	margin-bottom: 20px;
	table.params {
		margin-bottom: 40px;
		font-size: 14px;
		tr {
			&:before {
				top: 11px;
			}
			td {
				padding: 5px 8px;
				&:first-child {
					padding-left: 16px;
					padding-right: 0;
				}
			}
		}
	}
	.add_product {
		.add__item {
			font-size: 15px;
			.icon {
				margin-right: 4px;
				height: 16px;
			}
		}
	}
}
.device-basic__column:first-child {
	flex: 0 0 46.2%;
	margin-right: 24px;
	padding-right: 17px;
}
.price_product {
	margin-bottom: 14px;
	.price__item_old {
		.price__value {
			font-weight: 700;
		}
	}
	.price__item {
		margin-right: 8px;
	}
}
.product-actions {
	margin-bottom: 26px;
}
.product-lables {
	margin-bottom: 8px;
}
.product-lables__text {
	margin-left: 32px;
	font-size: 15px;
	line-height: 1.2;
	margin-top: -6px;
}
.product-icons {
	justify-content: flex-start;
}
.product-icons__item {
	margin-right: 35px;
	&:last-child {
		margin-right: 0;
	}
}
.tab {
	padding: 12px 16px;
}
.tab-container p {
	line-height: 1.45;
}
.tabs_product {
	margin-bottom: 30px;
}
.extra-info {
	grid-gap: 24px;
}
.extra-info__title {
	font-size: 18px;
	span {
		margin-left: 7px;
	}
}
.extra-info__list {
	padding-left: 20px;
}
.extra-info__list-item {
	font-size: 13px;
	margin-bottom: 15px;
	line-height: 1.35;
	&:before {
		left: -19px
	}
}
.content__main {
	.devices-block {
		padding-top: 40px;
		.title {
			font-size: 22px;
			font-weight: 400;
		}
	}
}
.content {
	padding-bottom: 0;
}
.device-full{
	&+.devices-block {
		.devices-block__wrap {
			margin: 0 -6px;
		}
		.devices-block__column {
			padding: 0 6px;
		}
		.device-nav {
			padding-top: 2px;
		}
	}
}
.fotorama__nav {
	text-align: left !important;
}
.cart-item {
	grid-template-columns: 90px 1fr 100px 75px 120px 20px;
	border-color: #c0c0c0;
	>* {
		padding-top: 12px;
		span {
			&:nth-child(2) {
				font-size: 14px;
			}
		}
	}
	.cart-item__info {
		padding: 10px 18px;
		padding-right: 0px
	}
	.cart-item__delete {
		padding: 4px 0;
		padding-top: 24px;
		a {
			color: #CCCCCC;
		}
	}
}
.cart-item__name {
	font-size: 14px;
	line-height: 1.3;
	margin-bottom: 12px;
}
.cart-item__image {
	padding: 0;
}
.extra-values {
	padding: 5px 6px;
	background: #F2F2F2;
}
.extra-values__row {
	grid-template-columns: 85px 1fr;
}
.extra-values__value {
	input {
		height: 28px;
		padding-left: 6px;
		font-size: 15px;
		border-color: #777;
	}
}
.extra-values__parameter {
	padding-left: 20px;
	font-size: 14px;
}
.cart-list__item {
	margin-bottom: 15px;
}
.cart-total {
	margin-top: 30px;
}
.cart-total__text {
    font-size: 22px;
	margin-right: 10px;
}
.cart-total__button {
	padding-right: 14px;
	padding-left: 14px;
	font-size: 15px;
	margin-right: 10px;
	background-color: #4DB0C7;
	border-color: #4DB0C7;
}
.top-info {
	background-color: #F2F2F2;
}
.main-menu {
	border-top-color: #c0c0c0;
}
.slider-nav .slick-dots li button {
	background-color: #a6a6a6;
}
.title {
	color: #333;
}
a {
	color: #0033dd;
}
.forever {
	color: #0033dd;
}
.footer__text {
	color: #333;
}
span.empty-cart {
	color: #333;
}
.price__item_old .price__value {
	color: #828282;
}
.add a {
	color: #828282;
}
.text_size_sm {
	color: #828282;
}
