.sections-block_wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: $gap-lg;
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
		grid-gap: $gap-md;
	}
	@media (max-width: $bp6) {
		grid-template-columns: repeat(1, 1fr);
		grid-gap: $gap-md;
	}
}
.banner-sections {
	position: relative;
}
.banner-sections__title {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	text-align: center;
	background-color: rgba(134, 134, 134, .74);
	color: #fff;
	text-transform: uppercase;
	font-size: 16px;
	letter-spacing: 1px;
	line-height: 1.2;
	height: 75px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 $gap-md;
}
.banner-sections__link {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	cursor: pointer;
}