.devices-block__wrap {
	// display: flex;
	margin: 0 -8px;
}
.devices-block__column {
	padding: 0 8px;
	min-width: 0px;
	display: flex;
}

.device {
	font-size: 13px;
	text-align: center;
	position: relative;
	display: flex;
	flex-direction: column;
	overflow: hidden;

	> * {
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
	}
	form {
		margin-top: auto;
	}
}
.device__image {
	margin-top: -16px;
	margin-right: -16px;
	margin-left: -16px;
}
.device__tags {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: column;
	padding-right: $gap-lg;
	align-items: flex-start;
	max-width: 100%;
}
.device__tag {
	margin-bottom: $gap-xs;
	&:last-child {
		margin-bottom: 0;
	}
}
.device__name {
	display: block;
	line-height: 1.3;
}
.device__price {
	display: flex;
	justify-content: center;
	font-weight: bold;
	> div {
		font-size: 14px;
		margin-right: $gap-sm;
		&:last-child {
			margin-right: 0;
		}
	}
}
.price__item_old {
	// color: red;
	// text-decoration: line-through;
	.price__value {
		color: $gray-2;
		position: relative;
		&:before {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 1px;
			background-color: rgb(204, 32, 32);
			top: 50%;

		}
	}
}
.order {
	display: flex;
	form {
		display: flex;
		> * {
			margin-right: $gap-xs;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
.device-order {
	margin-top: auto;
	justify-content: center;
}
.add {
	display: flex;
	justify-content: center;
	> * {
		margin-right: $gap-xs;
		&:last-child {
			margin-right: 0;
		}
	}
}
.add__item {
	align-items: center;
	font-size: 12px;
	.icon {
		height: 12px;
		margin-right: 0;
	}
}

.compare-items__item {
	.device {
		min-height: 410px;
	}
}








.device_row {
	flex-direction: row;
	text-align: left;
	border-bottom: 1px solid $gray-2;
	&:last-child {
		border: none;
	}
	> * {
		margin-bottom: 0;
	}
	.device__image {
		width: 150px;
		margin: 0;
	}
	.price {
		white-space: nowrap;
	}
	.order {
		justify-content: flex-end;
	}
	.add {
		flex-direction: column;
		justify-content: unset;
		padding-left: 50px;
	}
	.add__item {
		margin-bottom: $gap-sm;
		margin-right: 0;
	}
}
.device__column {
	margin-right: $gap-md;
	&:last-child {
		margin-right: 0;
	}
	&:nth-child(2) {
		flex: 1 1 auto;
	}
	> * {
		margin-bottom: $gap-md;
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.device__info {
	.params {
		tr {
			display: block;
			border: none;
		}
		td {
			font-size: 12px;
			display: inline-block;
			padding: 2px;
			font-weight: 400;
			&:first-child {
				color: $gray-2;
			}
		}
	}
}













//============= slick =================

.device {
	min-width: 0;
}

.device-nav {
	padding-top: $gap-sm;
	.slick-dots {
		display: flex;
		justify-content: center;
		align-items: center;
		li {
			margin-right: 8px;
			font-size: 0;
			button {
				font-size: 0;
				width: 11px;
				height: 11px;
				border-radius: 50%;
				background-color: $gray-2;
			}
			&:last-child {
				margin-right: 0;
			}
		}
		.slick-active {
			button {
				background-color: $primary-color;
			}
		}
	}

}
