.slider {
	background-color: #80A9DD;
	height: 400px;
	overflow: hidden;
}
.slider__item {
	display: flex;
	height: 400px;
	min-width: 0; // slick fix
}
.slider__content {
	flex: 0 0 100%;
}
.slider__image {
	// flex: 0 0 60%;
	// flex: 0 0 calc(100vw - (((100vw - 1030px) / 2) + (1030px * 0.4) + 10px));
	flex: 0 0 calc((100vw - 20px) - (1030px * 0.31) - (100vw - 20px - 1030px) / 2); // 20px - ширина полосы скроллинга в браузере; 1030px ширина контейнера;
	transform: translate(-100%, 0);
	position: relative;
	@media (max-width: 1030px) {
		flex-basis: 60%;
	}
	@media(max-width: $bp4) {
		flex-basis: 100%;
	}
	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.slider__container {
	height: 100%;
	@media (max-width: 1030px) {
		width: 100%;
	}
	@media (max-width: $bp4) {
		padding: 0;
	}
}
.slider__body {
	width: 31%;
	display: flex;
	flex-direction: column;
	height: 100%;
	color: #fff;
	padding-right: $gap-xl;
	align-items: flex-start;
	padding-top: 61px;
	@media (max-width: $bp1) {
		padding-left: $gap-xl;
	}
	@media(max-width: $bp4) {
		justify-content: center;
		width: 100%;
		position: relative;
		z-index: 2;
		background-color: $overlay-color;
		align-items: center;
		padding-right: $gap-xl;
		padding-left: $gap-xl;
	}
}
.slider__title {
	font-size: 42px;
	font-weight: 700;
	line-height: 1.3;
	letter-spacing: 1px;
	margin-bottom: 16px;
	@media (max-width: $bp3) {
		font-size: 24px;
	}
	@media (max-width: $bp4) {
		text-align: center;
		font-size: 36px;
	}
}
.slider__text {
	font-size: 19px;
	line-height: 1.5;
	margin-bottom: 37px;
	@media (max-width: $bp3) {
		font-size: 16px;
	}
	@media (max-width: $bp4) {
		text-align: center;
		font-size: 18px;
	}
}
.slider__button {
	font-size: 15px;
	background-color: #FFCC66;
	color: #333;
	padding: 12px 28px;

}
//======== slick =======================================================================
.slick-track {
	display: flex;
}
.slick-list {
	overflow: hidden;
}
.slider-nav {
	padding-top: 15px;
	.slick-dots {
		display: flex;
		justify-content: center;
		align-items: center;
		li {
			margin-right: 7.5px;
			font-size: 0;
			button {
				font-size: 0;
				width: 10.5px;
				height: 10.5px;
				border-radius: 50%;
				background-color: $gray-2;
			}
			&:last-child {
				margin-right: 0;
			}
		}
		.slick-active {
			button {
				background-color: $primary-color;
			}
		}
	}
	
}
