

// to device file?
.price__item {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}


.device-basic {
	display: flex;
	margin-bottom: $gap-xl;
	@media(max-width: $bp4) {
		flex-direction: column;
	}
}
.gallery {
	overflow: hidden;
	@media(max-width: $bp4) {
		margin-bottom: $gap-xl;
	}
}
.device-basic__column {
	&:first-child {
		flex: 0 0 40%;
		margin-right: $gap-lg;
		position: relative;
		@media(max-width: $bp4) {
			flex-basis: 100%;
			margin-right: 0;
		}
		.tags {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
		}
		.tag {
			margin-bottom: 5px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&:last-child {
		flex: 1 1 auto;
	}

}
.price_product {
	font-size: 24px;
	display: flex;
	margin-bottom: $gap-md;
	.price__item {
		margin-right: $gap-md;
		&:last-child {
			margin-right: 0;
		}
	}
	.price__item_old {
		.price__value {
			font-weight: 400;
		}
	}
	.price__label {
		font-size: 12px;
		color: $gray-2;
		margin-bottom: $gap-xs;
	}
	.price__value {
		font-size: 24px;
		font-weight: 700;
	}
}
.order_product {
	margin-right: $gap-md;
}
.add_product {
	display: flex;
	flex-direction: column;
	.add__item {
		margin-bottom: $gap-xs;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.product-actions {
	display: flex;
	margin-bottom: $gap-xl;
}


.product-lables {
	display: flex;
	margin-bottom: $gap-lg;
}
.product-lables__label {
	margin-right: $gap-sm;
	img {
		max-width: unset;
		max-height: unset;
	}
}
.product-lables__text {
	font-size: 12px;
	width: 150px;
}



.product-icons {
	display: flex;
	justify-content: space-between;
}
.product-icons__item {
	color: $gray-1;
	font-size: 16px;
}



.tabs_product {
	margin-bottom: $gap-lg;
}
.tabs__nav {
	display: flex;
	border-bottom: 1px solid $gray-2;
	margin-bottom: $gap-md;
}
.tab {
	display: flex;
	padding: $gap-sm $gap-md;
	margin-bottom: -1px;
}
.tab_active {
	background-color: #fff;
	border: 1px solid $gray-2;
	border-bottom-color: #fff;
	color: $gray-1;
}
.tab-container {
	p {
		line-height: 1.3;
	}
}



hr {
	border-bottom: 1px solid $gray-1;
	margin-bottom: $gap-lg;
}


.extra-info {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: $gap-md;
	@media(max-width: $bp4) {
		grid-template-columns: 1fr;
	}
}
.extra-info__title {
	margin-bottom: $gap-md;
	font-weight: 700;
	font-size: 20px;
	span {
		margin-left: 4px;
	}
}
.extra-info__list {
	padding-left: 16px;
}
.extra-info__list-item {
	font-size: 13px;
	margin-bottom: $gap-md;
	position: relative;
	line-height: 1.3;
	&:last-child {
		margin-bottom: 0;
	}
	&:before {
		content:"";
		display: block;
		width: 6px;
		height: 6px;
		background-color: $secondary-color;
		border-radius: 50%;
		position: absolute;
		top: 3px;
		left: -16px;
	}
}
table.params {
	display: block;
	tr {
		display: flex;
		position: relative;
		border: none;
		&:before {
			content:"";
			display: block;
			width: 6px;
			height: 6px;
			background-color: $secondary-color;
			border-radius: 50%;
			position: absolute;
			top: 8px;
			left: 0;
		}
		td {
			padding: 4px 8px;
			&:first-child {
				color: $gray-2;
				padding-left: 16px;
				padding-right: 4px;
				&:after {
					content: ":";
				}
			}
			&:last-child {
				font-weight: normal;
			}
		}
	}
}
table.full-params {
	th {
		text-align-last: left;
		padding-top: 16px;
		padding-bottom: 8px;
	}
}
.device__info {
	table.params {
		tr {
			&:before {
				display: none;
			}
		}
		td {
			&:first-child {
				padding-left: 0;
			}
		}
	}
}
