//pagination
$pagination-active-bgr: $secondary-color;
$pagination-fz: 14px;





.pagination {
	display: flex;
	align-items: baseline;
}
.pagination__label {
	margin-right: $gap-md;
}
.pagination__wrap {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 0 - $gap-sm;
}
.pagination__item {
	font-size: $pagination-fz;
	padding: $gap-xs;
	margin: 0 0 $gap-sm 0;
	min-width: 24px;
	text-align: center;
	&:last-child {
		margin-right: 0;
	}
}
.pagination__item_active {
	color: #fff;
	background-color: $pagination-active-bgr;
	margin-right: $gap-xs;
}