.top-info {
	font-size: 14px;
	padding: 7px 0;
	background-color: $top-header-background;
	@media(max-width: $bp2){
		font-size: 12px;
	}
	@media(max-width: $bp3){
		display: none;
	}
}
.top-info__wrap {
	display: flex;
}
.top-info__location {
	margin-right: 36px;
	a {
		color: rgb(0, 51, 204);
		font-size: 16px;
		span {
			margin-right: -5px;
		}
	}
}

.top-info__content {
	display: flex;
	flex: 1 1 auto;
	> * {
		margin-right: 40px;
		&:last-child {
			margin-right: 0;
			margin-left: auto;
		}
	}
	p {
		font-size: 14.2px;
		white-space: nowrap;
	}
	p:first-child {
		font-size: 14.5px;
	}
}
