input {
	&[type=number] {
		width: 60px;
		padding: 4px;
	}
}

input[type=checkbox] {
	margin-right: $gap-xs;
}

.input, select {
	font-size: 12px;
	line-height: 1;
	border: 1px solid $border-color;
	padding: 4px 8px;
}
.button {
	display: inline-block;
	white-space: nowrap;
	line-height: 1;
	font-size: $button-fz;
	padding: $button-p;
	// border-radius: $button-border-radius;
	background-color: $button-background;
	color: $button-color;
	border: 1px solid $button-border-color;
	min-height: 28px;

	&_secondary {
		background-color: $button-secondary-color;
		color: $button-secondary-color;
		border: 1px solid $button-secondary-border-color;
	}
}

.button_size_lg {
	font-size: $button-fz-lg;
	padding: $button-p-lg;
}
.order_product {
	button {
		@extend .button_size_lg;
	}
}
.input_size_lg {
	font-size: 16px;
}

.button_checkout {
	color: #fff;
	background-color: $secondary-color;
	border-color: $secondary-color;
}
.button:hover {
	background-color: darken($primary-color, 10%);
	border-color: darken($primary-color, 10%);
}
.button_2 {
	color: $button-2-color;
	background-color: $button-2-background;
	border: 1px solid $button-2-border-color;
}
.button_2:hover {
	background-color: darken($secondary-color, 3%);
	border-color: darken($secondary-color, 3%);
}
.button_checkout:hover {
	background-color: darken($secondary-color, 10%);
	border-color: darken($secondary-color, 10%);
}
.button_request {
	background-color: #fff;
}

.form__item {
	display: flex;
	flex-direction: column;
	margin-bottom: $gap-md;
	&:last-child {
		margin-bottom: 0;
	}
}
.form__element {
	font-size: 12px;
	line-height: 1;
	border: 1px solid $gray-2;
	padding: 4px 8px;
}
.form-label {
	font-size: 13px;
	margin-bottom: $gap-xs;
	font-weight: 700;
}
.form__text {
	margin-bottom: $gap-lg;
}
textarea {
	min-height: 100px;
}
