.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	.footer {
		margin-top: auto;
	}
}
.footer {
	padding-top: $gap-xl;
}
.footer__wrap {
	border-top: 2px solid black; //var
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	grid-gap: 24px;
	padding: 24px 0;
	@media (max-width: $bp3) {
		grid-template-columns: repeat(3, 1fr);
	}
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}@media (max-width: $bp5) {
		grid-template-columns: 1fr;
	}
}
.footer__column {
	> * {
		margin-bottom: 16px;
		&:last-child {
			margin: 0;
		}
	}
}
.footer__title {
	font-size: 15px;
	font-weight: 700;
}
.footer__text {
	font-size: 14px;
	line-height: 1.3;
	color: $gray-1;
	p {
		margin-bottom: 8px;
		&:last-child {
			margin: 0;
		}
	}
}
.forever {
	display: flex;
	font-size: 12px;
	color: $gray-1;
	line-height: 1;
	align-items: center;
	img {
		margin-right: 4px;
	}

}
.google-rating__stars {
	img {
		display: inline-block;
		height: 12px;
	}
}
.google-rating__text {
	font-size: 11px;
	color: $gray-2;
}
.footer__list {
	li {
		margin-bottom: 4px;
		&:last-child {
			margin: 0;
		}
	}
}