.header {
	padding: 22px 0 18px;
} 
.header__wrap {
	display: flex;
	@media (max-width: $bp3) {
		flex-wrap: wrap;
		justify-content: space-between;
	}
}
.header__column {
	margin-right: $gap-xxl;  // or var header__column ?
	&:last-child {
		margin-right: 0;
	}
	@media (max-width: $bp3) {
		margin-right: 0;
	}
}
.logo {
	height: 70px;
	margin-right: 70px;
	@media (max-width: $bp3) {
		height: 32px;
	}
}
.logo__image {
	max-height: 100%;
}
.header-search {
	flex: 1 1 auto;
	align-self: flex-start;
	margin-right: 57px;
	padding-top: 8px;
	@media (max-width: $bp3) {
		order: 1;
		flex-basis: 100%;
		margin-top: $gap-md;
	}
	form {
		display: flex;
	}
}
.header-search__input {
	flex: 1 1 auto;
	margin-right: $gap-sm;
	padding: 10px;
	border-color: $primary-color;
	font-size: 15px;
}
.header-search__button {
	padding: 8px 22px;
    font-size: 14px;
}

.header__column_links {
	display: flex;
	font-size: 14px;
	> div {
		// margin-right: $gap-xxl; // or var header__column ?
		&:last-child {
			margin-right: 0;
		}
		@media (max-width: $bp3) {
			margin-right: 0;
		}
		> * {
			margin-bottom: 6px;
			&:last-child {
				margin-bottom: 0;
			}
			@media (max-width: $bp3) {
				margin-bottom: 0;
			}
		}
	}
	@media (max-width: $bp3) {
		align-items: center;
	}
	.icon {
		@media (max-width: $bp3) {
			// margin-right: 20px;
			height: 20px;
			margin-right: 0;
		}
	}
	.icon-link {
		@media (max-width: $bp3) {
			margin-right: 20px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	.icon-link__item {
		font-size: 15px;
	}
}
.cart {
	margin-right: 66px;
	@media (max-width: $bp3) {
		span {
			display: none;
		}
	}
	.icon-link__item {
		font-weight: 700;
		font-size: 14px;
	}
}
.cart__item {
	margin-left: 28px;
	@media (max-width: $bp3) {
		display: none;
	}
}
.user {
	display: flex;
	flex-direction: column;
	@media (max-width: $bp3) {
		display: none;
	}
}
.links {
	display: flex;
	align-items: flex-start;
	@media (min-width: $bp3 + 1) {
		display: none;
	}
}