.side-menu {
	@media(max-width: $bp3) {
		display: none;
	}
}
.side-menu__item {
	padding: 9px;
	padding-left: $gap-md;
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 11px;
		left: 0px;
		border: 5px solid transparent;
		border-left-color: $secondary-color;
	}
}
.side-menu__item_level_1 {
	border-top: 1px solid $gray-2;
	&:first-child {
		border: none;
	}
	&:last-child {
		border-bottom: 1px solid $gray-2;
	}
}
.side-menu__link_active {
	font-weight: 700;
	color: #000; //var
}
.side-menu__item_level_2 {
	margin-left: $gap-md;
	font-size: 15px;
	&:before {
		border-left-color: $gray-2;
		border-width: 4px;
		top: 12px;
	}
}
.side-menu__item_level_3 {
	margin-left: $gap-xl;
	font-size: 13px;
	&:before {
		border-left-color: $gray-2;
		border-width: 4px;
		top: 9px;
	}
}

