//view
$view-fz: 14px;
$veiw-border-color: $gray-1;



.view {
	font-size: $view-fz;
}
.view_section {
	display: flex;
	align-items: center;
	margin-bottom: $gap-md;
	border-bottom: 2px solid $veiw-border-color;
	padding-bottom: $gap-sm;
	@media (max-width: $bp4) {
		display: none;
	}
}
.view__column {
	margin-right: $gap-lg;
	// font-size: 0;
	&:last-child {
		margin-left: auto;
		margin-right: 0;
	}
}